<template>
  <div class="login-container">
    <form class="login-form" @submit.prevent="login">
      <h2>Login</h2>
      <div class="input-group">
        <label for="username">Username</label>
        <input
          type="text"
          id="username"
          v-model="credentials.username"
          required
        />
      </div>
      <div class="input-group">
        <label for="password">Password</label>
        <input
          type="password"
          id="password"
          v-model="credentials.password"
          required
        />
      </div>
      <button type="submit" @click="login">Login</button>
      <p v-if="ifError">{{ ifError }}</p>
    </form>
  </div>
</template>

<script>
import { useAuthStore } from "@/stores/authStore";

export default {
  name: "LoginComponent",
  data() {
    return {
      credentials: {
        username: "",
        password: "",
      },
      deviceInfo: navigator.userAgent,
      latitude: "",
      longitude: "",
      accuracy: "",
    };
  },
  methods: {
    async login() {
      const authStore = useAuthStore();

      try {
        await authStore.login(
          this.credentials.username,
          this.credentials.password,
          this.deviceInfo,
          this.latitude,
          this.longitude
        );
        if (authStore.userData) {
          console.log("Login successful", authStore.userData);
          this.toHome();
        }
      } catch (error) {
        alert(error);
        console.error("Login failed:", error);
      }
    },

    getLocation() {
      navigator.geolocation.getCurrentPosition(this.success, this.error, {
        enableHighAccuracy: true, // Request high accuracy location
        timeout: 10000, // Time in milliseconds before the request times out
        maximumAge: 0, // Prevent the use of cached locations
      });
    },

    success(pos) {
      const crd = pos.coords;
      this.latitude = crd.latitude;
      this.longitude = crd.longitude;
      this.accuracy = `${crd.accuracy} meters `;
    },

    error(err) {
      console.warn(`ERROR(${err.code}): ${err.message}`);
    },

    toHome() {
      this.$router.push({ name: "HomeComponent" });
    },
  },

  computed: {
    ifError() {
      const authStore = useAuthStore();
      return authStore.loginError;
    },
  },

  mounted() {
    this.getLocation();
  },
};
</script>

<style scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(to top right, #3b5cc3 65%, black 35%);
  color: white;
  background-size: cover;
}

.login-form {
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  backdrop-filter: blur(10px);
  box-shadow: 0px 1px 2px rgba(255, 255, 255, 0.7);
  width: 300px;
  text-align: center;
}

.login-form h2 {
  margin-bottom: 1rem;
}

.input-group {
  margin-bottom: 1rem;
}

.input-group label {
  display: block;
  text-align: left;
  margin-left: 0.4rem;
  margin-bottom: 0.5rem;
}

.input-group input {
  width: 93%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button {
  width: 100%;
  padding: 0.5rem;
  border: none;
  background: black;
  border-radius: 4px;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  border: 1px solid ghostwhite;
}

button:hover {
  background: #3b5cc3;
}
</style>
