import { defineStore } from "pinia";
import axios from "axios";

export const useAuthStore = defineStore("auth", {
  state: () => ({
    userData: null,
    token: null,
    isLoggedIn: false,
    loginError: null,
  }),

  actions: {
    async login(email, password, deviceInfo, latitude, longitude) {
      try {
        const response = await axios.post(
          "https://mzquared.com/attend-api/v1/public/attendlogin",
          { email, password, deviceInfo, latitude, longitude }
        );
        if (response.data.success) {
          this.userData = response.data.data.attendUser;
          this.token = response.data.data.auth.token;
          this.isLoggedIn = true;
        }
      } catch (error) {
        this.loginError =
          "Access restricted. Please contact support for assistance.";
      }
    },
  },
});
